@import url(https://fonts.googleapis.com/css?family=Inter:400,700,900&display=swap);
*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
::-webkit-scrollbar {
  width: 0px;
}

a {
  text-decoration: none;
  color: black;
}

@media (min-width: 100px) and (max-width: 500px) {
  ::-webkit-scrollbar {
    width: 0px;
  }
}

.footer_wrapper {
  height: 400px;
  width: 100%;
  background: rgb(255, 255, 255);
  border: 5px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.column_one {
  height: 350px;
  width: 250px;
  background: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.column_two {
    height: 350px;
    width: 250px;
    background: rgb(255, 255, 255);
}

  .column_three {
    height: 350px;
    width: 250px;
    background: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .column_four {
    height: 350px;
    width: 250px;
    background: rgb(255, 255, 255);
   
  }
  .column_five {
    height: 150px;
    width: 150px;
    border-radius: 50%;
    background: rgb(0, 0, 0);
  }

  .column_one > div div,.column_three > div div{
    margin-bottom: 10px;
  }
.banner-ad {
  margin: 20px;
  background: white;
  height: 300px;
  box-shadow: 0px 20px 20px 5px #c7c7c74d;
  border-radius: 6px;
  background: url("https://i.ebayimg.com/images/g/klsAAOSwqKNcGtu7/s-l1600.webp");
  background-size: cover;
  display: flex;
  justify-content: space-between;
}

.bold-text {
  font-size: 50px;
  font-weight: 700;
}
.button-shop {
  display: inline;
  padding: 10px;
  line-height: 4;
  border: 3px solid #2196f3;
  border-radius: 6px;
  color: #2196f3;
}

.banner-text-l {
  height: 200px;
  width: 350px;
  margin: 50px;
}

.category-section {
  height: 100%;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 20px;
}

.category-section > div {
  height: 350px;
  width: 350px;
  margin: 50px 0px;
  background: #00bcd4;
  border-radius: 6px;
  box-shadow: 0px 20px 20px 1px #b1b1b121;
}
.full {
  position: absolute;
  background: red;
}

.preview {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 0));
  overflow-x: scroll;
}

.collection-item {
  width: 240px;
  height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}

.item-display {
  width: 100%;
  height: 95%;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(-77.25%, #00ffd1),
    to(rgba(0, 71, 255, 0))
  );
  background: linear-gradient(
    180deg,
    #00ffd1 -77.25%,
    rgba(0, 71, 255, 0) 100%
  );
  padding: 5px;
}

.collection-footer {
  font-size: medium;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.addition-button {
  background: transparent;
  width: 100%;
  height: 100%;
  float: right;
  margin: 0;
  border: none;
}

.addition-button:hover {
  background-color: #2196f32b;
}

button {
  width: 100%;
  border: 2px solid #2196f3;
  height: 40px;
  background: white;
  color: #2196f3;
  font-size: 14px;
  outline: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  margin-top: 20px;
}

button:hover {
  border: none;
  background-color: #2196f3;
  color: white;
  outline: none;
}

.shop-page{
    margin: 20px;
}
.checkout-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header_label{

  font-size: 90px;
}


.checkout-header {
  margin: 10px 60px;
  padding: 40px;
  display: grid;
  width: 50%;
  font-size: 13px;
  text-align: center;
  border-bottom: 5px solid #d3d3d342;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-gap: 10px;
}
.checkout-items {

  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}

.checkout-total{
  font-size: 50px;
  margin: 0px 0px 50px 0px;

}

@media (min-width: 100px) and (max-width: 500px) {
  .checkout-header {
    margin: 0;
    display: flex;
    justify-content: space-between;
    align-items: center; 
    padding: 20px;
    width: 100%;
    font-size: 13px;
}


}

.checkout-item {
  display: grid;
  width: 50%;
  padding: 40px;
  margin: 20px;
  font-size: 13px;
  text-align: center;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-gap: 10px;
  align-items: center;
}

.image-container {
  height: 100px;
}

.item-image {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(-77.25%, #00ffd1),
    to(rgba(0, 71, 255, 0))
  );
  background: linear-gradient(
    180deg,
    #00ffd1 -77.25%,
    rgba(0, 71, 255, 0) 100%
  );
  height: 100%;
  width: 100%;
  justify-self: center;
}

.quantity {
  display: flex;
  justify-content: center;
}

.quantity > span {
  margin: 0px 10px;
}

.arrowl,
.arrowr {
  cursor: pointer;
}

.remove-button {
  cursor: pointer;
}

@media (min-width: 100px) and (max-width: 500px) {
  .checkout-items {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .checkout-item {
    display: flex;
    width: 100%;
    margin: 0px;
    font-size: 13px;
    text-align: center;
    justify-content: space-between;
    align-items: center;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  margin: 20px;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
}

.logo {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 900;
}

a {
  text-decoration: none;
  color: black;
}
.options {
  display: flex;

  align-items: center;
}
.option {
  margin-right: 80px;
  cursor: pointer;
}
.option-name {
  padding: 6px;
  border-radius: 5px;
  border: 1px solid #e6e6e6;
  margin-right: 8px;
}

@media (min-width: 100px) and (max-width: 500px) {
  .options {
    display: grid;
    grid-gap: 10px;
  }
  .logged {
    display: grid;
    grid-gap: 10px;
  }
}

.cart-icon {
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.label-icon {
  width: 50px;
}

.item-count {
  position: absolute;
}


.cart-dropdown {
  flex-direction: column;
  height: 300px;
  border: 2px solid black;
  border-radius: 5px;
  width: 200px;
  right: 20px;
  display: flex;
  z-index: 9;
  padding: 20px;
  position: absolute;
  background: white;
  top: 80px;
  box-shadow: 7px 8px 9px #0000004d;
}
.cart-items {
  height: 200px;
  display: flex;
  flex-direction: column;
  overflow: scroll;
}

.cart-item {
  display: flex;
  margin: 10px 0px;
}

.image {
  width: 50%;
  background: aqua;
  margin-right: 15px;
}

.initial-message {
  color: lightgray;
  font-size: 20px;
  text-align: center;
  margin: auto auto;
}

.empty-cart-message {
  display: flex;
  text-align: center;
  margin: auto auto;
}

@media (min-width: 100px) and (max-width: 500px) {
  .cart-dropdown {
    top: 138px;
    width: 250px;
  }
}

.menu-item {
  border: 1px solid;
  margin: 20px 200px;
  padding: 0px 30px;
}

.menu-item:hover {
  cursor: pointer;
}

.directory{
    
    display: grid;
}

.wrapper {
  display: flex;
  margin-top: 50px;
  flex-direction: column;
}

.sign-in {
  padding: 20px 20px;
}

form {
  padding: 40px 0px;
}

.form-input {
  border: 1px solid #8080804a;
  height: 0px;
  width: 100%;
  padding: 18px 12px 18px 12px;
  outline: none;
  margin: 10px 0;
  font-size: 14px;
  font-weight: 100;
  border-radius: 4px;
  display: flex;
}
.group {
  display: flex;
  align-items: center;
}


.registration {
  display: flex;
  justify-content: space-around;
}

@media (min-width: 100px) and (max-width: 500px) {
  .registration {
    display: block;
  }
}

.page_wrapper{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.inner_box{
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
}
.heading{
    font-size: 100px;
}
